import "./reset.scss"
import "./style.scss"
import {TweenMax} from 'gsap';

class Index extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      siteAnnoying: false
    };
  }
  componentDidMount() {
    this.animate();
  }
  render() {
    const buttonText = this.state.siteAnnoying ? "this website is annoying" : "this website is boring";
    const buttonClass = this.state.siteAnnoying ? "annoying" : "boring";

    return (
      <div className={buttonClass}>
        <h1>emma rae norton aka "doodybrains"</h1>
        <img className="gif" src={require('../static/wow.gif')} />
        <button onClick={toggleSite.bind(this)}>{buttonText}</button>

        <div className="that-part thingz-i-made-for-friendz">
          <p>thingz-i-made-for-friendz</p>
          <a href="#" className="link">erica eso</a>
          <a href="#" className="link">ariel yelen</a>
          <a href="#" className="link">jack reynolds</a>
        </div>
        <div className="that-part thingz-i-made-at-shift-lab-ny">
          <p>thingz-i-made-at-shift-lab-ny</p>
          <a href="#" className="link">uprise art</a>
          <a href="#" className="link">aldrich art museum</a>
          <a href="#" className="link">snap on mobile app</a>
          <a href="#" className="link">airlift creative</a>
        </div>
        <div className="that-part thingz-i-made-for-clientz">
          <p>thingz-i-made-for-clientz</p>
          <a href="#" className="link">placeholder</a>
          <a href="#" className="link">woman in the cabinet</a>
        </div>
        <div className="that-part thingz-i-made-for-myself-and-for-you">
          <p>thingz-i-made-for-myself-and-for-you</p>
          <a href="#" className="link">website with the sound of its own making</a>
          <a href="#" className="link">javascript scrabble app</a>
          <a href="#" className="link">undecided.js</a>
          <a href="#" className="link">construction site</a>
          <a href="#" className="link">know thyself</a>
          <a href="#" className="link">mixtape app</a>
          <a href="#" className="link">sol lewitt - wall drawing #959 (4 the browser)</a>
        </div>
        <div className="that-part thingz-to-know">
          <p>thingz-to-know</p>
          <a href="#" className="link">website with the sound of its own making is for sale at Printed Matter</a>
          <a href="#" className="link">website with the sound of its own making is for sale through Rhizome at the Internet Yama-Ichi</a>
          <a href="#" className="link">website with the sound of its own making is featured on Brutalist Websites</a>
          <a href="#" className="link">I am a masters candidate at ITP NYU</a>
        </div>
      </div>
    );
  }

  animate() {
    const link = document.getElementsByClassName('link');
    TweenMax.to(link, 20, {bezier:[{x:10, y:30}, {x:0, y:20}, {x:-30, y:20}, {x:15, y:0}], ease: 'Linear.easeNone', repeat: -1, yoyo:true});
  }
}

function toggleSite() {
  this.setState({
    siteAnnoying: !this.state.siteAnnoying
  })
}

export default Index;



